import "./App.css";

import React, { Suspense } from "react";
const HelpProject = React.lazy(() => import("./components/HelpProject"));
const Header = React.lazy(() => import("./components/Header"));
const Footer = React.lazy(() => import("./components/Footer"));
const DiseaseList = React.lazy(() => import("./components/DiseaseList"));
const ProcessListPage = React.lazy(() =>
  import("./components/ProcessListPage")
);
function App() {
  return (
    <div className='App'>
      <Suspense fallback={<div>Loading...</div>}>
        <Header />
        <DiseaseList />
        <HelpProject />
        <ProcessListPage />
        <Footer />
      </Suspense>
    </div>
  );
}

export default App;
